<template>
  <div class="page">
    <!-- 页头 -->
    <AnimateHeader
      bg-color="#F5F5F5 "
      menu-bg-color="#F0F0F0"
      color="#3A3A3A"
      fill="#3A3A3A"
      stroke="#3A3A3A"
      title="支付宝提现"
      :on-back-button-click="back"
      :expanded="false"
      :animate="false"
    >
    </AnimateHeader>
    <div class="page-content withdraw-alipay">

      <div class="alipay-input alipay-account">
        <div class="title">支付宝账号</div>
        <label>
          <input type="text" :style="{fontSize : accountFontSize + 'px'}" v-model="form.account" placeholder="输入手机或邮箱">
        </label>
      </div>
      <div class="alipay-input alipay-name">
        <div class="title">姓名</div>
        <label >
          <input type="text" v-model="form.real_name" :value="dd" placeholder="输入真实姓名">
        </label>
      </div>

      <withdraw-options :options="alipayInfo.withdrawLists" :selected-index="selectedIndex" :on-select="onSelect"/>

      <div class="tips-content">
        <span v-show="selected">消耗咖币 <span class="assertive fontSemibold">{{alipayInfo.realPricesHash[selectedIndex]}}</span>，</span><span>当前剩余咖币 <span class="assertive fontSemibold">{{info.balance}}</span></span>
        <div class="tips">注：支付宝官方每笔提现收取0.5元手续费</div>
      </div>

      <button :disabled="buttonDisabled" :class="{'button-active': !buttonDisabled}" class="button button-withdraw" @click="submit()">
        提现
      </button>
    </div>
  </div>
</template>
<style lang="scss" scoped>
  @import "../../../scss/mixins";

  .withdraw-alipay {
    margin-top: 50px;
    background: #FFF;
    padding: 25px 30px 25px 30px;

    .alipay-input {
      width: 200px;
      border-bottom: 1px solid #ddd;
      padding-bottom: 3px;

      .title {
        font-size: 12px;
        color: #B5B5B5;
        line-height: 17px;
        height: 17px;

        @include font-regular();
      }

      label {
        display: block;
        height: 33px;
        width: 100%;

        input {
          transition: font-size 0.1s ease-in;
          height: 33px;
          width: 100%;
          display: inline-block;
          line-height: normal;
          font-size: 24px;
          color: #3A3A3A;
          padding: 0;

          @include font-regular();

          &::-webkit-input-placeholder {
            font-size: 24px;
            color: #DDDDDD;
            line-height: 33px;
            height: 33px;

            @include font-regular();
          }
        }
      }
    }

    .alipay-account {
      margin-bottom: 20px;
    }

    .tips-content {
      height: 41px;
      font-size: 13px;
      color: #484746;

      @include font-regular();

      .tips {
        margin-top: 5px;
        line-height: 18px;
      }

      .fontSemibold {
        @include font-semibold();
      }
    }

    .button-withdraw {
      width: 172px;
      height: 40px;
      min-height: 40px;
      background: #DDDDDD;
      border-radius: 2px;
      line-height: 40px;
      margin-top: 30px;
      font-size: 13px;
      color: #FFFFFF;

      @include font-medium();
    }

    .button.disabled, .button[disabled] {
      opacity: 1;
    }

    .button-active {
      background: #EF4F2A;
    }
  }
</style>
<script>
  import withdrawOptions from './WithdrawOptions.vue'
  import AnimateHeader from '@/widget/animate-header/Index.vue'
  import { popup_wechat_bind, popup_user_freeze } from '../popup.js'
  import {mapState, mapActions, mapGetters} from 'vuex'

  export default {
    components: {
      withdrawOptions,
      AnimateHeader
    },

    data () {
      return {
        selected: true,
        selectedIndex: 0,
        form: {
          account: '',
          real_name: '',
          price: 0
        }
      }
    },

    computed: {
      ...mapState('withdraw', [
        'info'
      ]),

      ...mapGetters('withdraw', [
        'alipayInfo'
      ]),

      buttonDisabled () {
        if (this.form.account !== '' && this.form.real_name !== '') {
          return false
        }

        return true
      },

      accountFontSize () {
        let len = this.form.account.length
        let fontSize = 24
        if (len >= 16 && len < 24) {
          fontSize = 16
        }
        if (len >= 24 && len < 27) {
          fontSize = 14
        }
        if (len >= 27 && len < 29) {
          fontSize = 13
        }
        if (len >= 29 && len < 32) {
          fontSize = 12
        }
        return fontSize
      }
    },

    mounted () {
      this.fetch()
    },

    methods: {
      fetch () {
        this.getWithdrawList().then(() => {
          this.form.account = this.alipayInfo.account
          this.form.real_name = this.alipayInfo.realName
        })
      },
      ...mapActions('withdraw', [
        'getWithdrawList',
        'submitAlipay'
      ]),

      back () {
        $router.back('/withdraw/')
      },

      onSelect (index) {
        this.selected = true
        this.selectedIndex = index
      },

      submit () {
        if (this.submitLock) return
        this.submitLock = true
        setTimeout(() => { this.submitLock = false }, 1000)
        if (this.info.alipay.enabled === 0) { // 需要绑定微信
          let p = popup_wechat_bind
          let popup = $popup.fromTemplate(p.template, p.options)
          popup.show().then(btn => {
            if (btn === 0) { // 取消
            } else if (btn === 1) { // 前往微信绑定页面
              $router.forward({
                path: '/user/bindWeixin',
                query: { backroute: '/withdraw/alipay', jumproute: '/withdraw/alipay' }
              })
            }
          })
          return
        }
        this.form.price = this.alipayInfo.amountHash[this.selectedIndex]

        let account = this.form.account
        if (!this.form.account) {
          $toast.show('账号不能为空')
          return
        }

        let isNotPhone = !/^1[3456789]\d{9}$/.test(account)
        let isNotEmail = !/@\w+\.\w+/.test(account)

        if ((isNotPhone && isNotEmail) || (isNotEmail && isNotPhone)) {
          $toast.show('请输入正确的支付宝帐号')
          return
        }

        let real_name = this.form.real_name

        if (!real_name) {
          $toast.show('姓名不能为空')
          return
        }

        let test_real_name = real_name.replace(/·/g, '') // 允许输入 ·  so先替换掉再判断中文
        if (/([^\u4e00-\u9fa5])+/gi.test(test_real_name)) {
          $toast.show('真实姓名必须是中文')
          return
        }

        if (!this.form.price) {
          $toast.show('请选择提现金额')
          return
        }

        this.submitAlipay(this.form).then((res) => {
          if (res.status === 200) {
            if (res.data.err_code === 90000) {
              let popup = $popup.fromTemplate(
                popup_user_freeze.template(),
                popup_user_freeze.options
              )
              popup.show()
            } else {
              $router.forward({
                path: '/withdraw/result/alipay'
              })
            }
          }
        })
      }
    }
  }
</script>



